@import '../../styles/sass/variables';


.title {
    font-family: 'Josefin Sans', sans-serif;
    font-size: 17px;
    line-height: 1.2;
    letter-spacing: 3px;
    margin:30px 0 30px 0;
    font-weight: 200;
    text-transform: capitalize;
    color: $textColor;
    position: relative;
    .title h2 {
        margin: 0;

    }
    /*псевдоэлемент*/
    &::before {
        content: "";
        position: absolute;
        background: $primaryColor;
        width: 55px;
        height: 1px;
        bottom: -20px;
        /*выпавнивание элемента по отношению в родителю */
        left: 50%;
        transform: translate(-50%, 50%);
    }

}




